import React from 'react'
import { Button, Container, Modal } from 'react-bootstrap'

export default class EmployeeAdd extends React.Component {
	constructor() {
		super()
		this.state = {
			modalVisible: false,
		}
		this.handleSubmit = this.handleSubmit.bind(this)
		this.handleShowModal = this.handleShowModal.bind(this)
		this.handleHideModal = this.handleHideModal.bind(this)
	}


	handleShowModal() {
		this.setState({ modalVisible: true })
	}

	handleHideModal() {
		this.setState({ modalVisible: false })
	}


	handleSubmit(e) {
		console.log(e)
		const form = document.forms.employeeAdd
		const employee = {
			name: form.name.value,
			extension: form.extension.value,
			email: form.email.value,
			title: form.title.value
		}
		this.props.createEmployee(employee)
		form.name.value = ''
		form.extension.value = ''
		form.email.value = ''
		form.title.value = ''
		this.setState({ modalVisible: false })
	}

	render() {
		return (
			<>
				<div>
					<Button className='addEmployee' variant='primary' size='sm' onClick={this.handleShowModal}>New Employee</Button>
				</div>
				<Modal show={this.state.modalVisible} onHide={this.handleHideModal}>
					<Modal.Header closeButton><Modal.Title>Add New Employee</Modal.Title></Modal.Header>
					<Modal.Body>
						<Container fluid>
							<form name="employeeAdd">
								Name: <input type="text" name="name" /><br />
								Extension: <input type="text" name="extension" maxLength={4} /><br />
								Email: <input type="text" name="email" /><br />
								Title: <input type="text" name="title" /><br />
							</form>
						</Container>
					</Modal.Body>
					<Modal.Footer>
						<Button type="submit" variant="success" size='sm' className='mt-4'
							onClick={this.handleSubmit}>Add Employee</Button>
					</Modal.Footer>
				</Modal>
			</>
		)
	}
}